@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Roboto Slab", serif;
}

p {
  text-align: center;
  margin: 1em;
  color: #fff;
  font-size: 0.8em;
}
h2 {
  text-align: center;
}

@media only screen and (min-width: 1200px) {
  p {
    font-size: 1em;
  }
  .divEdit {
    height: 100vh;
  }
}

@media only screen and (min-width: 1200px) {
  .fnt {
    font-size: 2em;
    color: #fff;
  }

  .recharts-legend-item-text {
    font-size: 0.8em;
    color: #000;
  }
}

body {
  color: #fff;
}

.main {
  text-align: center;
}

.flexContent {
  display: flex;
  width: 100%;
  flex-direction: row;
  justify-content: space-evenly;
}

.brd {
  border-right: 1px solid white;
  padding: 1em;
}

form {
  margin: 1em;
}

input {
  padding: 1em;
  text-align: center;
  font-size: 1em;
}

h3 {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 0.9em;
}

h4 {
  font-size: 0.7em;
  margin-top: 0;
  margin-bottom: 0;
}

@media only screen and (min-width: 1200px) {
  h4 {
    font-size: 1em;
  }
  h3 {
    font-size: 1.5em;
  }
}

